import TabBar from '@/components/TabBar/index.vue'
import LeftMenu from '@/components/LeftMenu/index.vue'
import { dateFtt } from '@/utils/common.js'
import $ from 'jquery'

export default {
    data() {
        return {
            username: '昵称',
            sex: '男',
            birthday: '1991年10月20号',
            address: '浙江杭州',
            currentDate: new Date(),
            columns: ['男', '女'],
            dateShow: false,
            sexShow: false
        }
    },
    created: function () {
        $.ajax({
            type: "GET",
            url: "/Mobile/Api/Info",
            data: {},
            dataType: "json",
            success: function(rst){
                if(rst.status==0){
                    this.username = rst.U_NickName;
                    this.sex = rst.U_Gender==1?"男":rst.U_Gender==2?"女":"未设置";
                    this.birthday = dateFtt('yyyy年MM月dd日', new Date(rst.U_Birthday));
                    this.address = rst.U_Address;
                }
                else{
                    alert(rst.msg);
                }
            }
        });
    },
    components: {
        TabBar,
        LeftMenu
    },
    methods: {
        //日期选择：取消按钮点击事件
        handleDateCancel() {
            this.dateShow = false
        },
        //日期选择：确定按钮点击事件
        handleDateConfirm(val) {
            this.dateShow = false
            this.birthday = dateFtt('yyyy年MM月dd日', new Date(val))
        },
        //出生年月日点击事件
        handleBirthdayClick() {
            this.dateShow = true
        },
        //性别选择点击事件
        handleSexClick() {
            this.sexShow = true
        },
        //性别选择器change事件
        onChange(picker, value,index) {
            this.sexShow = false
            this.sex = value
        },
        
        //获取用户基本信息
        getUserInfo(){
           
        }
    }
}